*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  gap: 1rem;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
}

header,
#game-container,
#result-display {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#game-container {
  width: 100%;
  max-width: 600px;
  min-height: 500px;
}

#game-stats {
  display: flex;
  flex-direction: row;
}

.stats {
  padding: 1rem;
}

#computer-stats {
  text-align: right;
}

/* Utility class to hide elements */
.hidden {
  display: none !important;
}

/* Utility class for screen reader only text */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* Health Bar */
.bar-wrapper {
  position: relative;
  width: 100px;
  height: 20px;
  overflow: hidden;
  border: 1px solid black;
}

.bar {
  position: relative;
  height: 100%;
  transition: width 0.3s ease;
  background-color: green; /* updated by class like .full/.half/.zero */
  display: block;
  overflow: hidden;
}

.bar-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  white-space: nowrap;
}

/* Health states */
.bar.full {
  width: 100%;
  background-color: #4caf50;
}

.bar.half {
  width: 50%;
  background-color: #ff9800;
}

.bar.zero {
  width: 5%;
  background-color: #f44336;
}

/* Spinner */

.spinner {
  margin: 5rem auto;
  text-align: center;
}

.spinner svg {
  height: 4rem;
  width: 4rem;
  fill: #808080;
  animation: rotate 2s infinite linear;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(1px);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 7vh;
}
