*, :before, :after {
  box-sizing: border-box;
  gap: 1rem;
  margin: 0;
  padding: 0;
}

body {
  flex-direction: column;
  align-items: center;
  margin: 1rem;
  display: flex;
}

header, #game-container, #result-display {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#game-container {
  width: 100%;
  max-width: 600px;
  min-height: 500px;
}

#game-stats {
  flex-direction: row;
  display: flex;
}

.stats {
  padding: 1rem;
}

#computer-stats {
  text-align: right;
}

.hidden {
  display: none !important;
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.bar-wrapper {
  border: 1px solid #000;
  width: 100px;
  height: 20px;
  position: relative;
  overflow: hidden;
}

.bar {
  background-color: green;
  height: 100%;
  transition: width .3s;
  display: block;
  position: relative;
  overflow: hidden;
}

.bar-text {
  pointer-events: none;
  white-space: nowrap;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bar.full {
  background-color: #4caf50;
  width: 100%;
}

.bar.half {
  background-color: #ff9800;
  width: 50%;
}

.bar.zero {
  background-color: #f44336;
  width: 5%;
}

.spinner {
  text-align: center;
  margin: 5rem auto;
}

.spinner svg {
  fill: gray;
  width: 4rem;
  height: 4rem;
  animation: 2s linear infinite rotate;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

#overlay {
  z-index: 1000;
  backdrop-filter: blur(1px);
  background-color: #fffc;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  padding-top: 7vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}
